.App {
  font-family: sans-serif;
  width: 100%;
  display: flex;
}
.home {
  width: 30%;
}
.cart {
  width: 70%;
  margin-left: 20px;
  margin-top: 20px;
  border: 1px solid black;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;
  border: 2px solid red;
}
.price {
  margin-right: 70px;
}

.qty {
  border: 1px solid gray;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.qty .qty-button {
  cursor: pointer;
}
